    color: var(--Black-light1);
    font-size: 15px;
    font-family: var(--Open);
    margin: 0px 0px 15px;
}

.contact-up-text-tell {
    color: var(--Black-light1);
    font-size: 15px;
    font-family: var(--Open);
}

.contact-up-text-tell a,
.up-text-left a,
.up-text-right a {
    color: var(--Orange);
    text-decoration: none;
}

.contact-up-text-tell a:hover,
.up-text-left a:hover,
.up-text-right a:hover {
    color: var(--Black-light1);
    transition: .5s ease;
}


/* Button */

.contact-up-text-btn {
    margin: 30px 0px 20px 0px;
}

.contact-up-btn {
    text-align: center;
    font-weight: 900;
    font-size: 18px;
    font-family: var(--Playfair);
    color: var(--Orange);
    background-color: var(--White-light-pro3);
    padding: 20px 32px;
    border-radius: 50px;
    border: 3px solid var(--Orange);
    text-transform: uppercase;
}

.contact-up-btn:hover {
    color: var(--White);
    background-color: var(--Orange);
    transition: 0.4s ease-in-out;
}


/* Contact Form */

.contact-down-text {
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 6rem 0rem 5rem 0rem;
}

.contact-down-left,
.contact-down-right {
    padding: 0rem 9rem 0rem 9rem;
}

.contact-down-left h1 {
    color: var(--Candy-1);
    font-family: var(--Delius);
    font-size: 30px;
    line-height: 48px;
}

.contact-down-left p {
    color: var(--Gray-3);
    font-family: var(--Open);
    font-size: 15px;
    padding: 10px 20px 10px 0px;
}

input[type=text],
[type=Email],
select,
textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
}

.contact-down-right {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}


/* Submit Button */

.contact-book-btn {
    margin: 20px 0px 20px 0px;
}

.contact-sub-btn {
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    font-family: var(--Playfair);
    color: var(--Orange);
    background-color: var(--White-light-pro3);
    padding: 15px 25px;
    border-radius: 50px;
    border: 2px solid var(--Orange);
    text-transform: uppercase;
    letter-spacing: 3px;
}

.contact-sub-btn:hover {
    color: var(--White);
    background-color: var(--Orange);
    transition: 0.5s ease-in-out;
}


/* ====================== */


/*   Tab        Device   */


/* =====================*/

@media (max-width: 1024px) {
    .up-text-left,
    .up-text-right {
        margin: 0rem 3rem 0rem 3rem;
    }
    .contact-down-text {
        margin: 3rem 0rem 3rem 0rem;
    }
    .col-6 {
        width: 100%;
    }
    .contact-down-left,
    .contact-down-right {
        padding: 1rem 12rem 0rem 3rem;
    }
    .contact-down-right {
        background-color: var(--White);
    }
}


/* ====================== */


/*   Mobile     Device   */


/* =====================*/

@media (max-width: 768px) {
    .up-text-left,
    .up-text-right {
        margin: 0rem 1rem 2rem 1rem;
    }
    .contact-up-btn {
        font-size: 15px;
        padding: 16px 16px;
    }
    .contact-down-text {
        margin: 1rem 0rem 3rem 0rem;
    }
    .col-6 {
        width: 100%;
    }
    .contact-down-left,
    .contact-down-right {
        padding: 1rem 1rem 0rem 1rem;
    }
    .contact-down-right {
        background-color: #f2f2f2;
    }
}
.scrollTop-btn {
    position: fixed;
    width: 100%;
    left: 95%;
    bottom: 40px;
    height: 20px;
    font-size: 2rem;
    z-index: 1;
    cursor: pointer;
    color: var(--Gray-6);
    display: inline-block;
}

@media (max-width: 1024px) {
    .scrollTop-btn {
        left: 50%;
        font-size: 2.5rem;
    }
}

@media (max-width: 768px) {
    .scrollTop-btn {
        left: 85%;
        font-size: 1.5rem;
    }
}
